import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import { room_pool_rules_config_create } from "../../../../../../api/room";

// import { room_pool_rules_config_create } from "../../../api/room";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, rid } = props;
  const [form] = Form.useForm();
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        await room_pool_rules_config_create({
          ...form.getFieldsValue(),
        });
        message.success("创建成功");
        onOk();
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 7 },
    wrapperCol: { span: 17 },
  };

  const on_cancel = () => {
    onCancel();
    form.resetFields();
  };
  useEffect(() => {
    if(visible){
      form.setFieldsValue({
        rid,
      });
    }
  }, [visible]);
  return (
    <Modal
      title="添加平衡规则"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="房间id"
          name="rid"
          rules={[{ required: true, message: "请输入房间id" }]}
        >
          <InputNumber disabled placeholder="请输入房间id" />
        </Form.Item>
        <Form.Item
          label="标志"
          name="flag"
          rules={[{ required: true, message: "请选择标志" }]}
        >
          <Select placeholder="请选择标志">
            <Option value={1}>收分</Option>
            <Option value={2}>放分</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="资金池最小值"
          name="threshold_min"
          rules={[{ required: true, message: "请输入资金池最小值" }]}
        >
          <InputNumber  placeholder="请输入资金池最小值" />
        </Form.Item>
        <Form.Item
          label="资金池最大值"
          name="threshold_max"
          rules={[{ required: true, message: "请输入资金池最大值" }]}
        >
          <InputNumber  placeholder="请输入资金池最大值" />
        </Form.Item>
        <Form.Item
          label="每局触发概率"
          name="trigger_fac"
          rules={[{ required: true, message: "请输入每局触发概率" }]}
        >
          <InputNumber  placeholder="请输入每局触发概率" />
        </Form.Item>
        <Form.Item
          label="每局成功后修正概率"
          name="trigger_delta_fac"
          rules={[{ required: true, message: "请输入每局成功后修正概率" }]}
        >
          <InputNumber  placeholder="请输入每局成功后修正概率" />
        </Form.Item>
        <Form.Item
          label="机器人最小分数"
          name="score_min"
          rules={[{ required: true, message: "请输入机器人最小分数" }]}
        >
          <InputNumber  placeholder="请输入机器人最小分数" />
        </Form.Item>
        <Form.Item
          label="机器人最大分数"
          name="score_max"
          rules={[{ required: true, message: "请输入机器人最大分数" }]}
        >
          <InputNumber  placeholder="请输入机器人最大分数" />
        </Form.Item>
        <Form.Item
          label="状态"
          name="status"
          rules={[{ required: true, message: "请选择状态" }]}
        >
          <Select placeholder="请选择状态">
            <Option value={0}>启用</Option>
            <Option value={1}>关闭</Option>
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
