import React, { useEffect, useRef, useState } from "react";
import {
  Form,
  Table,
  InputNumber,
  Input,
  Button,
  Divider,
  message,
  Space,
} from "antd";

import Page from "components/Page";
import { getRobotConfig, updateRobotBasic } from "api/robot";
import { formatCash, formatTtype } from "utils/format";
import AddForm from "./PlayRule/Add";
// import BalanceForm from "./PlayRule/Balance";
import CardSetting from "./PlayRule/components/CardSetting";
import UpdateCardSetting from "./PlayRule/components/UpdateCardSetting";
import DisCardSetting from "./PlayRule/components/DisCardSetting";
import { room_config_get, room_config_set } from "../../api/room";
import RobotRoomRole from "./PlayRule/components/RobotRoomRole";
import ListTable from "./PlayRule/components/ListTable";
import CreateRule from "./PlayRule/components/CreateRule";
import UpdateRule from "./PlayRule/components/UpdateRule";
function PlayRule(props) {
  const [addVisible, setAddVisible] = useState(false);
  const [balanceVisible, setBalanceVisible] = useState(false);
  const [balance, setBalance] = useState({ id: 0, data: {} });
  const [form] = Form.useForm();
  const [data, setData] = useState([]);
  const [editingKey, setEditingKey] = useState("");
  const childRef = useRef();
  const [id, set_id] = useState(""); //房间id
  const [discard_visible, set_discard_visible] = useState(false);
  const [card_visible, set_card_visible] = useState(false); //特殊出牌设置显隐状态
  const [update_card_visible, set_update_card_visible] = useState(false); //特殊出牌设置编辑
  const [update_rule_visible, set_update_rule_visible] = useState(false);
  const [role_visible, set_role_visible] = useState(false); //机器人规则配置
  const [create_rule_visible, set_create_rule_visible] = useState(false); //创建规则显示隐藏
  const [info, set_info] = useState({});
  const fetchData = async () => {
    try {
      // const data = await room_config_get();

      // let dataSource = [];
      // if (data.code === 0) {
      //   for (let i in data.data) {
      //     let item = data.data[i];
      //     let temp = [];
      //     temp.key = item.id;
      //     temp.id = item.id;
      //     temp.ttype = item.ttype;
      //     temp.player = item.player;
      //     temp.difen = item.difen;

      //     let basic = JSON.parse(item.basic);
      //     temp.round1 = basic.playWithRange[0];
      //     temp.round2 = basic.playWithRange[1];
      //     temp.act1 = basic.waitActGameTime[0];
      //     temp.act2 = basic.waitActGameTime[1];
      //     temp.join1 = basic.waitJoinGameTime[0];
      //     temp.join2 = basic.waitJoinGameTime[1];
      //     temp.min = basic.userLen[0];
      //     temp.max = basic.userLen[1];

      //     temp.balance = item.balance;

      //     dataSource.push(temp);
      //   }
      // }

      setData(data.data);

      // console.log("data", data.data);
    } catch (error) {
      // message.error(error)
      console.error(error);
      // message.error(error.message);
    }
  };

  const save = async (key) => {
    console.log("save key===", key);
    try {
      const row = await form.validateFields();
      const newData = [...data];
      const index = newData.findIndex((item) => key === item.key);

      console.log(row, "row---->");

      const basic = {};
      basic.playWithRange = [row.round1 | 0, row.round2 | 0];
      basic.waitActGameTime = [row.act1 | 0, row.act2 | 0];
      basic.waitJoinGameTime = [row.join1 | 0, row.join2 | 0];
      basic.userLen = [row.min | 0, row.max | 0];

      const res = await updateRobotBasic({
        id: key,
        basic: JSON.stringify(basic),
      });

      if (!res) return message.error("更新失败！");
      message.success("更新成功！");
      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        setData(newData);
        setEditingKey("");
      } else {
        newData.push(row);
        setData(newData);
        setEditingKey("");
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };

  const configureBalance = (id, data) => {
    setBalance({ id, data });
    setBalanceVisible(true);
  };

  useEffect(() => {
    fetchData();
  }, []);

  const isEditing = (record) => record.key === editingKey;

  const edit = (record) => {
    form.setFieldsValue({ ...record });
    console.log("record", record);
    setEditingKey(record.key);
  };
  //创建规则取消
  const create_rule_cancel_h = () => {
    set_create_rule_visible(false);
  };
  //创建规则取消
  const create_rule_ok_h = () => {
    set_create_rule_visible(false);
    childRef.current.getData();
  };
  const EditableCell = ({
    editing,
    dataIndex,
    title,
    inputType,
    record,
    index,
    children,
    ...restProps
  }) => {
    const inputNode = inputType === "number" ? <InputNumber /> : <Input />;
    return (
      <td {...restProps}>
        {editing ? (
          <Form.Item
            name={dataIndex}
            style={{
              margin: 0,
            }}
            rules={[
              {
                required: true,
                message: `Please Input ${title}!`,
              },
            ]}
          >
            {inputNode}
          </Form.Item>
        ) : (
          children
        )}
      </td>
    );
  };
  const room_config_set_h = async (rid, status) => {
    await room_config_set({
      id: rid,
      status,
    });
    childRef.current.getData();
    message.success("设置成功");
  };
  //设置机器人房间
  const set_room_role_h = (id) => {
    set_id(id);
    set_role_visible(true);
  };
  //机器人房间设置确定
  const robot_room_ok = () => {
    set_role_visible(false);
  };
  //机器人房间设置取消
  const robot_room_cancel = () => {
    set_role_visible(false);
  };
  const columns = [
    {
      title: "序号",
      dataIndex: "id",
      key: "id",
      fixed: "left",
    },
    {
      title: "房间id",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "玩法",
      dataIndex: "ttype",
      key: "ttype",
      render: (ttype) => formatTtype(ttype),
    },
    {
      title: "人数",
      dataIndex: "player",
      key: "player",
      render: (player) => (player ? player : "练习场"),
    },
    {
      title: "底注",
      dataIndex: "difen",
      key: "difen",
      render: (difen) => (difen ? formatCash(difen) : ""),
    },
    {
      title: "入桌条件",
      editable: true,
      children: [
        {
          title: "最小真人数量",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大真人数量",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "进入数量",
      editable: true,
      children: [
        {
          title: "最小机器人数量",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大机器人数量",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "带入规则",
      editable: true,
      children: [
        {
          title: "最小机器人数量",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大机器人数量",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "加入房间时间",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "等待入桌超时离桌时间",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "离桌局数",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "离桌赢分范围",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "离桌输分范围",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "行为（摸/打/弃）时间",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "round1",
          key: "round1",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "round2",
          key: "round2",
          editable: true,
        },
      ],
    },
    {
      title: "特殊出牌规则设置",
      render: (_, record) => {
        return <a onClick={() => set_cardSetting_h(record.id)}>设置</a>;
      },
    },
    {
      title: "弃牌设置",
      render: (_, record) => {
        return <a onClick={() => set_disCard_h(record.id)}>设置</a>;
      },
    },
    {
      title: "房间池设置",

      render: (_, record) => {
        return <a onClick={() => set_room_role_h(record.rid)}>设置</a>;
      },
    },
    {
      title: "机器人是否有效",
      dataIndex: "status",
      key: "status",
      render: (_, record) => {
        return <span>{record.status == 0 ? "有效" : "无效"}</span>;
      },
    },
    {
      title: "操作",
      dataIndex: "operation",
      width: 150,
      fixed: "right",
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <a
              onClick={() => save(record.key)}
              style={{
                marginRight: 8,
              }}
            >
              保存
            </a>
            <a onClick={() => setEditingKey("")}>返回</a>
          </span>
        ) : (
          <Space>
            <a onClick={() => edit(record)}>编辑</a>
            {record.status == 0 ? (
              <a onClick={() => room_config_set_h(record.rid, 1)}>关闭</a>
            ) : (
              <a onClick={() => room_config_set_h(record.rid, 0)}>开启</a>
            )}
          </Space>
        );
      },
    },
  ];

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }
    if (col.children) {
      return {
        ...col,
        children: [
          {
            ...col.children[0],
            onCell: (record) => {
              return {
                record,
                inputType: "text",
                dataIndex: col.children[0].dataIndex,
                title: col.children[0].title,
                editing: isEditing(record),
              };
            },
          },
          {
            ...col.children[1],
            onCell: (record) => {
              return {
                record,
                inputType: "text",
                dataIndex: col.children[1].dataIndex,
                title: col.children[1].title,
                editing: isEditing(record),
              };
            },
          },
        ],
      };
    }
    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: "text",
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });
  //机器人弃牌设置
  const set_disCard_h = (id) => {
    set_id(id);
    set_discard_visible(true);
  };
  //机器人弃牌确定
  const discard_Ok = () => {
    set_discard_visible(false);
  };
  //特殊出牌规则设置
  const set_cardSetting_h = (id) => {
    set_id(id);

    set_card_visible(true);
  };
  //特殊出牌设置规则取消
  const card_cancel_h = () => {
    set_card_visible(false);
  };
  //特殊出牌规则设置确定
  const card_Ok_h = () => {
    set_card_visible(false);
  };

  //修改特殊出牌设置规则取消
  const update_card_cancel_h = () => {
    set_update_card_visible(false);
  };
  //修改特殊出牌规则设置确定
  const update_card_Ok_h = () => {
    set_update_card_visible(false);
  };
  //房间设置更新
  const update_rule_ok_h = () => {
    childRef.current.getData();
    set_update_rule_visible(false);
  };
  //房间设置更新取消
  const update_rule_cancel_h = () => {
    set_update_rule_visible(false);
  };
  //房间更新
  const room_config_update_h = (record) => {
    set_info(record);
    set_update_rule_visible(true);
  };
  return (
    <Page title="场次规则管理">
      <Button type="primary" onClick={() => set_create_rule_visible(true)}>
        添加规则
      </Button>
      <Divider dashed />
      {/* <Form form={form} component={false}>
        <Table
          components={{
            body: {
              cell: EditableCell,
            },
          }}
          scroll={{ x: 4100 }}
          bordered
          pagination={false}
          columns={mergedColumns}
          dataSource={data}
        />
      </Form> */}
      <ListTable
        ref={childRef}
        set_cardSetting_h={set_cardSetting_h}
        set_room_role_h={set_room_role_h}
        room_config_set_h={room_config_set_h}
        room_config_update={room_config_update_h}
        set_disCard_h={set_disCard_h}
      />

      {/* <AddForm
        visible={addVisible}
        onCancel={() => setAddVisible(false)}
        onSuccess={() => fetchData()}
      /> */}

      {/* <BalanceForm
        visible={balanceVisible}
        data={balance}
        onCancel={() => setBalanceVisible(false)}
        onSuccess={() => fetchData()}
      /> */}
      <CardSetting
        id={id}
        visible={card_visible}
        onCancel={() => card_cancel_h()}
        onOk={() => card_Ok_h()}
      />
      <UpdateCardSetting
        id={id}
        visible={update_card_visible}
        onCancel={() => update_card_cancel_h()}
        onOk={() => update_card_Ok_h()}
      />
      <DisCardSetting
        id={id}
        visible={discard_visible}
        onCancel={() => set_discard_visible(false)}
        onOk={() => discard_Ok()}
      />
      <RobotRoomRole
        visible={role_visible}
        id={id}
        onOk={() => robot_room_ok()}
        onCancel={() => robot_room_cancel()}
      />
      <CreateRule
        visible={create_rule_visible}
        onCancel={() => create_rule_cancel_h()}
        onOk={() => create_rule_ok_h()}
      />
      <UpdateRule
        info={info}
        visible={update_rule_visible}
        onCancel={() => update_rule_cancel_h()}
        onOk={() => update_rule_ok_h()}
      />
    </Page>
  );
}

export default PlayRule;
