import React, { useState, useEffect } from 'react';
import { Modal, Form, Input, Button, Upload, Select, message } from 'antd';
import { addRobotConfig } from 'api/robot'
import { ttypeArr } from 'constants/ttypes';

const { Option } = Select;

function EditForm(props) {
  const { visible, onCancel, onSuccess } = props;

  const [form] = Form.useForm();
  const [ttypeValue, setTtypeValue] = useState('');


  const submit = async () => {
    const submitData = form.getFieldsValue();

    let basic = {};
    basic.waitJoinGameTime = [parseInt(submitData.join1), parseInt(submitData.join2)];
    basic.waitActGameTime = [parseInt(submitData.act1), parseInt(submitData.act2)];
    basic.playWithRange = [parseInt(submitData.round1), parseInt(submitData.round2)];
    basic.userLen = [parseInt(submitData.min), parseInt(submitData.max)];

    let params = {};
    params.ttype = parseInt(submitData.ttype);
    params.player = parseInt(submitData.player);
    params.difen = parseInt(submitData.difen);
    params.basic = JSON.stringify(basic);
    console.log("params---", params);

    const res = await addRobotConfig(params);
    if (!res) {
      return message.error("添加失败！")
    }

    message.success("添加成功");

    console.log(submitData, "submitData");
    form.resetFields();
    onSuccess();
    onCancel();
  };

  return (
    <Modal visible={visible} onOk={submit} onCancel={onCancel} title="添加规则" okText="确定" cancelText="返回" maskClosable={false} closable={false} getContainer={false}
    >
      <Form form={form} name="form_in_modal" labelCol={{ span: 6 }} wrapperCol={{ span: 16 }}>
        <Form.Item label='玩法' name='ttype'>
          <Select onChange={v => setTtypeValue(v)}>
            {ttypeArr.map(v => <Option key={v["ttype"]} value={v["ttype"] + ''}>{v['name']}</Option>)}
          </Select>
        </Form.Item>

        <Form.Item label='人数' name='player' >
          <Select >
            <Option value="0">练习场</Option>
            <Option value="2">2</Option>
            <Option value="6">6</Option>
          </Select>
        </Form.Item>

        {ttypeValue !== '2' && <Form.Item label='底注' name='difen'>
          <Select>
            <Option value="0">练习场</Option>
            <Option value="1">0.01</Option>
            <Option value="5">0.05</Option>
            <Option value="10">0.1</Option>
            <Option value="25">0.25</Option>
            <Option value="50">0.5</Option>
            <Option value="100">1</Option>
            <Option value="200">2</Option>
            <Option value="300">3</Option>
            <Option value="500">5</Option>
            <Option value="1000">10</Option>
            <Option value="2000">20</Option>
            <Option value="3000">30</Option>
            <Option value="4000">40</Option>
            <Option value="5000">50</Option>
            <Option value="8000">80</Option>
            <Option value="10000">100</Option>
          </Select></Form.Item>}

          {ttypeValue === '2' && <Form.Item label='底注' name='difen'>
            <Select>
            <Option value="0">练习场</Option>
            <Option value="2500">25</Option>
            <Option value="5000">50</Option>
            <Option value="10000">100</Option>
            <Option value="25000">250</Option>
            <Option value="50000">500</Option>
            <Option value="100000">1000</Option>
            <Option value="200000">2000</Option>
            <Option value="300000">3000</Option>
          </Select></Form.Item>}



        <Form.Item label="玩牌局数">
          <Input.Group>
            <Form.Item
              name="round1"
              noStyle
              rules={[{ required: true, message: 'Province is required' }]}
            >
              <Input style={{ width: '50%' }} />
            </Form.Item>
            <Form.Item
              name="round2"
              noStyle
              rules={[{ required: true, message: 'Street is required' }]}
            >
              <Input style={{ width: '50%' }} />
            </Form.Item>
          </Input.Group>
        </Form.Item>

        <Form.Item label="加入房间时间">
          <Input.Group>
            <Form.Item
              name="join1"
              noStyle
            >
              <Input style={{ width: '50%' }} />
            </Form.Item>
            <Form.Item
              name="join2"
              noStyle
            >
              <Input style={{ width: '50%' }} />
            </Form.Item>
          </Input.Group>
        </Form.Item>

        <Form.Item label="出牌时间">
          <Input.Group>
            <Form.Item
              name="act1"
              noStyle
            >
              <Input style={{ width: '50%' }} />
            </Form.Item>
            <Form.Item
              name="act2"
              noStyle
            >
              <Input style={{ width: '50%' }} />
            </Form.Item>
          </Input.Group>
        </Form.Item>

        <Form.Item label='人数下限' name='min'>
          <Input />
        </Form.Item>

        <Form.Item label='人数上限' name='max'>
          <Input />
        </Form.Item>

      </Form>
    </Modal>
  );
}
export default EditForm;
