import React, { useEffect, useState, useRef } from "react";
import { Table, Button, Popconfirm, Space, message, Divider } from "antd";
// import Page from "../../components/Page";
import ListTable from "./components/ListTable";
import CreateRoomConf from "./components/CreateRoomConf";
import CreateRoomRule from "./components/CreateRoomRule";
import UpdateRoomRule from "./components/UpdateRoomRule";
import Modal from "antd/lib/modal/Modal";

function Index(props) {
  const { visible, onCancel, onOk, id } = props;
  const childRef = useRef();
  const [room_conf_visible, set_room_conf_visible] = useState(false);
  const [room_rule_visible, set_room_rule_visible] = useState(false);
  const [update_room_rule_visible, set_update_room_rule_visible] =
    useState(false);
  const [rule_info, set_rule_info] = useState({});
  //添加房间池配置
  const handleRoomConfAdd = () => {
    set_room_conf_visible(true);
  };
  //添加平衡池设置
  const handleRoomRuleAdd = () => {
    set_room_rule_visible(true);
  };
  //房间池配置取消
  const room_conf_cancel = () => {
    set_room_conf_visible(false);
  };
  //房间池配置成功
  const room_conf_ok = () => {
    set_room_conf_visible(false);
  };
  //房间池规则取消
  const room_rule_cancel = () => {
    set_room_rule_visible(false);
  };
  //房间池规则成功
  const room_rule_ok = () => {
    childRef.current.getData();
    set_room_rule_visible(false);
  };
  //修改房间池规则取消
  const update_room_rule_cancel = () => {
    set_update_room_rule_visible(false);
  };
  //修改房间池规则成功
  const update_room_rule_ok = () => {
    childRef.current.getData();
    set_update_room_rule_visible(false);
  };
  //房间池规则编辑
  const update_h = (record) => {
    set_rule_info(record);
    set_update_room_rule_visible(true);
  };
  useEffect(() => {
    if (visible) {
      childRef.current.clearData();
      childRef.current.getData();
    }
  }, [visible]);
  return (
    <Modal
      title="房间池设置"
      visible={visible}
      onOk={onOk}
      onCancel={onCancel}
      width={820}
    >
      <Space direction="vertical" size={30}>
        <CreateRoomConf
          visible={visible}
          onCancel={() => room_conf_cancel()}
          onOk={() => room_conf_ok()}
          rid={id}
        />
        <Space>
          {/* <Button type="primary" onClick={() => handleRoomConfAdd()}>
          添加房间池配置
        </Button> */}
          <Button type="primary" onClick={() => handleRoomRuleAdd()}>
            添加平衡规则
          </Button>
        </Space>
      </Space>
      <Divider dashed />
      <ListTable ref={childRef} update_h={update_h} rid={id} />
      <CreateRoomRule
        visible={room_rule_visible}
        onCancel={() => room_rule_cancel()}
        onOk={() => room_rule_ok()}
        rid={id}
      />
      <UpdateRoomRule
        visible={update_room_rule_visible}
        onCancel={() => update_room_rule_cancel()}
        onOk={() => update_room_rule_ok()}
        info={rule_info}
        rid={id}
      />
    </Modal>
  );
}
export default Index;
