import { Table, Button, message, Popconfirm } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import {
  room_pool_rules_config_get,
  room_pool_rules_config_set_status,
} from "../../../../../../api/room";
// import {
//   room_pool_rules_config_get,
//   room_pool_rules_config_set_status,
// } from "../../../api/room";
// import mock from ''
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      room_pool_rules_config_get_handle(newVal);
    },
    clearData: () => {
      setData([]);
    },
  }));
  const {
    modifypassword_h,
    modifyaccountRole_h,
    block_h,
    pageSize = 10,
    total = 0,
    update_h,
    showTotal = true,
    rid,
  } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 10,
    total: total || 0,
    showSizeChanger: true,
  });
  //转换标志
  const flag_trans_text = (type) => {
    let text;
    switch (type) {
      case 1:
        text = "收分";
        break;
      case 2:
        text = "放分";
        break;
    }
    return text;
  };
  const room_pool_rules_config_set_status_h = async (status, rule_id) => {
    try {
      await room_pool_rules_config_set_status({
        status,
        id: rule_id,
      });
      message.success("状态设置成功");
      room_pool_rules_config_get_handle();
    } catch (err) {
      console.error(err);
    }
  };
  const columns = [
    {
      title: "规则id",
      dataIndex: "rule_id ",
      fixed: "left",
      width: 100,
      render: (_, record) => <span>{record.rule_id}</span>,
    },
    {
      title: "标志",
      dataIndex: "flag",
      render: (_, record) => <span>{flag_trans_text(record.flag)}</span>,
    },
    { title: "房间池最小值", dataIndex: "	threshold_min" },
    { title: "房间池最大值", dataIndex: "threshold_max" },
    { title: "每局触发概率", dataIndex: "trigger_fac" },
    { title: "胜/负后递增概率", dataIndex: "trigger_delta_fac" },
    { title: "机器人发牌最小分数", dataIndex: "score_min" },
    { title: "机器人发牌最大分数", dataIndex: "score_max" },
    {
      title: "状态",
      dataIndex: "status",
      render: (state) => (state == 0 ? "有效" : "无效"),
    },

    {
      title: "操作",
      dataIndex: "operation",
      align: "center",
      width: 200,
      render: (text, record, index) => {
        return (
          <span>
            <Button
              type="link"
              onClick={() => {
                update_h(record);
              }}
            >
              编辑
            </Button>
            <Popconfirm
              title={`确定${record.status == 0 ? "关闭" : "启用"}这条规则？`}
              onConfirm={() =>
                room_pool_rules_config_set_status_h(
                  record.status == 0 ? 1 : 0,
                  record.rule_id
                )
              }
              okText="确定"
              cancelText="取消"
            >
              <Button type="link">
                {record.status == 0 ? "关闭" : "启用"}
              </Button>
            </Popconfirm>
          </span>
        );
      },
    },
  ];
  //获取列表
  const room_pool_rules_config_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await room_pool_rules_config_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        rid,
        ...params,
      });
      // console.log(data)
      setData(data.data);
        setPagination({
        ...pagination,
        total: data.page.count,
      });
      // console.log(pagination);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    // console.log(pagination)
    setPagination({ ...pagination });
  };
  useEffect(() => {
    room_pool_rules_config_get_handle();
  }, [pagination.current, pagination.pageSize]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        scroll={{ x: 1600 }}
        rowKey="rid"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
