import ttypes from 'constants/ttypes';
import {awsUploadFile, downloadFromS3, downloadFromS3Url} from "./aws";
import {Image} from "antd";
import React from "react";
import {addSessionStorage, getSessionStorage} from "./storageUtil";
import store from 'store'
const cdnUrl = store.getState().cdnUrl;
const s3Enable = store.getState().s3Enable;

export const formatUrl = pic => {
  if (pic.substr(0, 4) === "http") {
    return pic;
  }
  // return process.env.REACT_APP_CDN_URL + "/" + pic;

  return cdnUrl + "/" + pic;
};

export const formatCash = cash => {
  const number = (cash/100) || 0;
  return number;
  // return '₹' + number
};


export const formatTtype = ttype => {
  return ttypes[ttype] || "";
};

export const toPercent = (point, len = 2) => {
  let str = Number(point * 100).toFixed(len);
  str += "%";
  return str;
};

export const formatUrlS3 = async (pic,appid) => {
    if(!pic){
        return
    }
    if (pic.substr(0, 4) === "http") {
        return pic;
    }
    //判断是否为s3拉取
    if (!s3Enable) {
        return cdnUrl + "/" + pic;
    }
    //从缓存中取pic
    var picStor = getSessionStorage(pic);
    if(picStor){
        return picStor;
    }

    let key= pic;
  let resp = null;
  try {
    resp = await downloadFromS3(key,appid);
  } catch (error) {
    resp = null
    console.error("download failed ,"+error)
  }

  if(resp == null ){
    return "";
  }
  let suffer= pic.substr(pic.lastIndexOf(".")+1);
  var imageUrl = "data:image/"+suffer+";base64," + encode(resp);

    //添加缓存
    addSessionStorage(pic,imageUrl);
  return imageUrl;
};

function encode(data)
{
  var str = data.reduce(function(a,b){ return a+String.fromCharCode(b) },'');
  return btoa(str).replace(/.{76}(?=.)/g,'$&\n');
}
