import axios from "utils/axios";

export const getRobotConfig = async (params) => {
  return await axios.get(`/robot/configs`, { params });
};

export const addRobotConfig = async (params) => {
  const data = await axios.post(`/robot/config`, params);
  return data?.data;
};

export const getRobotUsers = async (params) => {
  const data = await axios.get(`/robot/users`, { params });
  return data?.data;
};

export const updateRobotBalance = async (params) => {
  const data = await axios.patch(`/robot/config/balance`, params);
  return data?.data;
};

export const updateRobotBasic = async (params) => {
  const data = await axios.patch(`/robot/config/basic`, params);
  return data?.data;
};

export const addRobotUser = async (params) => {
  const data = await axios.post(`/robot/user`, params);
  return data?.data;
};

export const deleteRobotUser = async (id) => {
  const data = await axios.delete(`/robot/user/` + id);
  return data?.data;
};

export const addRobotGame = async (uid, room_ids) => {
  const data = await axios.post(`/robot/game`, {
    uid,
    // ttype_key: ttypeKey,
    room_ids,
  });
  return data?.data;
};

export const deleteRobotGame = async (id) => {
  const data = await axios.delete(`/robot/game/` + id);
  return data?.data;
};

export const setRobotStatus = async (uid, status) => {
  const data = await axios.patch(`/robot/status/status`, { uid, status });
  return data?.data;
};

export const setRobotTime = async (uid, stime, etime) => {
  const data = await axios.patch(`/robot/status/time`, { uid, etime, stime });
  return data?.data;
};

export const setRobotUname = async (params) => {
  const data = await axios.patch(`/robot/user/uname`, params);
  return data?.data;
};

export const setRobotPic = async (params) => {
  const data = await axios.patch(`/robot/user/pic`, params);
  return data?.data;
};

export const setRobotDeposit = async (params) => {
  const data = await axios.patch(`/robot/account/deposit`, params);
  return data?.data;
};

export const setRobotWithdrawable = async (params) => {
  const data = await axios.patch(`/robot/account/withdrawable`, params);
  return data?.data;
};

export const deleteRobot = async (uid) => {
  let data = await axios.delete(`/robot/users/${uid}`);
  return data?.data;
};

export const updateRobotCoins = async (uidArr, amount) => {
  let data = await axios.patch(`/robot/updateCoins`, { uids: uidArr, amount });
  return data?.data;
};

