import { Table, Button, Space, Popconfirm } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import { room_config_get } from "../../../../api/room";
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      room_config_get_handle(newVal);
    },
  }));
  const {
    pageSize = 10,
    total = 0,
    showTotal = true,
    set_cardSetting_h,
    set_room_role_h,
    room_config_set_h,
    room_config_update,
    set_disCard_h,
  } = props;
  const [editingKey, setEditingKey] = useState("");
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 10,
    total: total || 0,
    showSizeChanger: true,
  });

  // const room_config_update = (record) => {
  //   setEditingKey(record.key);
  // };
  //转换房间
  const trans_room_type = (type) => {
    let text;
    switch (type) {
      case 1:
        text = "1-13Points";
        break;
      case 2:
        text = "2-10Card";
        break;
      case 3:
        text = "3-Pool";
        break;
    }
    return text;
  };
  const columns = [
    // {
    //   title: "序号",
    //   dataIndex: "id",
    //   key: "id",
    //   fixed: "left",
    // },
    {
      title: "房间id",
      dataIndex: "rid",
      key: "rid",
      fixed: "left",
    },
    {
      title: "玩法",
      dataIndex: "room_type",
      key: "room_type",
      render: (_, record) => <span>{trans_room_type(record.room_type)}</span>,
    },
    
    {
      title: "玩法类型说明",
      dataIndex: "game_type",
      key: "game_type",
    },
    {
      title: "人数",
      dataIndex: "max_player",
      key: "max_player",
    },
    {
      title: "底注",
      dataIndex: "ante",
      key: "difen",
      // ante: (difen) => (difen ? formatCash(difen) : ""),
    },
    {
      title: "入桌条件",
      editable: true,
      children: [
        {
          title: "最小真人数量",
          dataIndex: "human_min",
          key: "human_min",
          editable: true,
        },
        {
          title: "最大真人数量",
          dataIndex: "human_max",
          key: "human_max",
          editable: true,
        },
      ],
    },
    {
      title: "进入数量",
      editable: true,
      children: [
        {
          title: "最小机器人数量",
          dataIndex: "wingman_min",
          key: "wingman_min",
          editable: true,
        },
        {
          title: "最大机器人数量",
          dataIndex: "wingman_max",
          key: "wingman_max",
          editable: true,
        },
      ],
    },
    {
      title: "带入规则",
      editable: true,
      children: [
        {
          title: "最小带入底注倍数",
          dataIndex: "buy_in_min",
          key: "buy_in_min",
          editable: true,
        },
        {
          title: "最大带入底注倍数",
          dataIndex: "buy_in_max",
          key: "buy_in_max",
          editable: true,
        },
      ],
    },
    {
      title: "加入房间时间",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "join_interval_min",
          key: "join_interval_min",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "join_interval_max",
          key: "join_interval_max",
          editable: true,
        },
      ],
    },
    {
      title: "等待入桌超时离桌时间",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "wait_2_leave_time_min",
          key: "wait_2_leave_time_min",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "wait_2_leave_time_max",
          key: "wait_2_leave_time_max",
          editable: true,
        },
      ],
    },
    {
      title: "离桌局数",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "leave_round_min",
          key: "leave_round_min",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "leave_round_max",
          key: "leave_round_max",
          editable: true,
        },
      ],
    },
    {
      title: "离桌赢分范围",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "leave_win_score_min",
          key: "leave_win_score_min",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "leave_win_score_max",
          key: "leave_win_score_max",
          editable: true,
        },
      ],
    },
    {
      title: "离桌输分范围",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "leave_lose_score_min",
          key: "leave_lose_score_min",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "leave_lose_score_max",
          key: "leave_lose_score_max",
          editable: true,
        },
      ],
    },
    {
      title: "行为（摸/打/弃）时间",
      editable: true,
      children: [
        {
          title: "最小",
          dataIndex: "action_time_min",
          key: "action_time_min",
          editable: true,
        },
        {
          title: "最大",
          dataIndex: "action_time_max",
          key: "action_time_max",
          editable: true,
        },
      ],
    },
    {
      title: "特殊出牌规则设置",
      render: (_, record) => {
        return <a onClick={() => set_cardSetting_h(record.rid)}>设置</a>;
      },
    },
    {
      title: "弃牌设置",
      render: (_, record) => {
        return <a onClick={() => set_disCard_h(record.rid)}>设置</a>;
      },
    },
    {
      title: "房间池设置",
      render: (_, record) => {
        return <a onClick={() => set_room_role_h(record.rid)}>设置</a>;
      },
    },
    {
      title: "机器人是否有效",
      dataIndex: "status",
      key: "status",
      render: (_, record) => {
        return <span>{record.status == 0 ? "有效" : "无效"}</span>;
      },
    },
    {
      title: "操作",
      dataIndex: "operation",
      width: 150,
      fixed: "right",
      render: (_, record) => (
        <Space>
          <a onClick={() => room_config_update(record)}>编辑</a>
          <Popconfirm
            title={`是否确认${record.status == 0 ? "关闭" : "开启"}这条规则`}
            onConfirm={() =>
              room_config_set_h(record.rid, record.status == 0 ? 1 : 0)
            }
            okText="确认"
            cancelText="取消"
          >
            {record.status == 0 ? <a>关闭</a> : <a>开启</a>}
          </Popconfirm>
        </Space>
      ),
    },
  ];
  //获取列表
  const room_config_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await room_config_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      // console.log(data);

      
      setData(data.data);
        setPagination({
        ...pagination,
        total: data.page.count,
      });
      setLoading(false);
    } catch (e) {
      setLoading(false);
      // console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    setPagination({ ...pagination });
  };
  useEffect(() => {
    room_config_get_handle();
  }, [pagination.current, pagination.pageSize]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        scroll={{ x: 4100 }}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
