import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
  Card,
} from "antd";
import { Tree } from "antd";
import {
  room_money_pool_create,
  room_money_pool_get,
  room_money_pool_update,
} from "../../../../../../api/room";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, rid } = props;
  const [form] = Form.useForm();
  const [state, set_state] = useState(0); //0添加,1修改
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        if (state == 1) {
          await room_money_pool_update({
            ...form.getFieldsValue(),
          });
          message.success("修改成功");
        } else {
          await room_money_pool_create({
            ...form.getFieldsValue(),
          });
          message.success("创建成功");
        }

        // form.resetFields();
        onOk();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  //获取基本配置
  const get_data = async () => {
    let data = await room_money_pool_get({
      rid: rid,
    });
    if (data) {
      set_state(1);
      form.setFieldsValue({
        ...data,
        rid,
      });
    } else {
      form.resetFields();
      form.setFieldsValue({
        rid,
      });
      set_state(0);
    }
  };
  useEffect(() => {
    get_data();
  }, [rid]);
  return (
    // <Modal
    //   title="添加房间池配置"
    //   visible={visible}
    //   onCancel={on_cancel}
    //   onOk={submit}
    // >
    <Card
      title="房间池规则配置"
      actions={[
        <Button type="primary" onClick={submit}>
          {`${state == 1 ? "修改配置" : "添加配置"}`}
        </Button>,
      ]}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="房间ID"
          name="rid"
          rules={[{ required: true, message: "请输入房间ID" }]}
        >
          <InputNumber disabled placeholder="请输入房间ID" />
        </Form.Item>
        <Form.Item
          label="生效"
          name="status"
          rules={[{ required: true, message: "请选择生效状态" }]}
        >
          <Select placeholder="请选择生效状态">
            <Option value={0}>启用</Option>
            <Option value={1}>停用</Option>
          </Select>
        </Form.Item>

        <Form.Item
          label="房间池初始值"
          name="init_money"
          rules={[{ required: true, message: "请输入房间池初始值" }]}
        >
          <InputNumber placeholder="请输入房间池初始值" />
        </Form.Item>
        <Form.Item
          label="抽取率"
          name="rake_fac"
          rules={[{ required: true, message: "请输入抽取率" }]}
        >
          <InputNumber
            placeholder="请输入抽取率"
            addonAfter="%"
            controls={false}
          />
        </Form.Item>
      </Form>
    </Card>
    // </Modal>
  );
};
export default memo(Index);
