import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  Radio,
  Divider,
  Card,
  Table,
  InputNumber,
  Popconfirm,
  Typography,
} from "antd";
import {
  dropcard_config_create,
  dropcard_config_get,
  dropcard_config_update,
} from "../../../../api/room";
// import { accrount_create } from "../../../api/acount";
// import { role_create } from "../../../api/role";
const { Option } = Select;
const { TextArea } = Input;
const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === "number" ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};
const defaultData = [
  {
    id: 2,
    player: 2,
    min_score: 0,
    fac: 0,
  },
  {
    id: 3,
    player: 3,
    min_score: 0,
    fac: 0,
  },
  {
    id: 4,
    player: 4,
    min_score: 0,
    fac: 0,
  },
  {
    id: 5,
    player: 5,
    min_score: 0,
    fac: 0,
  },
  {
    id: 6,
    player: 6,
    min_score: 0,
    fac: 0,
  },
];
const Index = (props) => {
  const { onCancel, visible, onOk, id } = props;
  const [form] = Form.useForm();
  const [Tableform] = Form.useForm();
  const [editableKeys, setEditableRowKeys] = useState([]);
  const [dataSource, setDataSource] = useState(defaultData);
  const [editingKey, setEditingKey] = useState("");

  const [state, set_state] = useState(0); //0为添加，1为修改

  const isEditing = (record) => record.id === editingKey;

  const edit = (record) => {
    Tableform.setFieldsValue({
      ...record,
    });
    setEditingKey(record.id);
  };

  const cancel = () => {
    setEditingKey("");
  };

  const save = async (key) => {
    try {
      const row = await Tableform.validateFields();
      const newData = [...dataSource];
      const index = newData.findIndex((item) => key === item.id);
      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        setDataSource(newData);
        setEditingKey("");
      } else {
        newData.push(row);
        setDataSource(newData);
        setEditingKey("");
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };
  //转换dataSource
  const data_source_trans = (dataSource) => {
    let subObj = {};
    dataSource.map((item) => {
      subObj[`fighter_${item.player}_fac`] = item.fac;
      subObj[`fighter_${item.player}_min_score`] = item.min_score;
    });
    return subObj;
  };
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue(), dataSource);
    // return;
    try {
      form.validateFields().then(async () => {
        if (state == 0) {
          await dropcard_config_create({
            ...form.getFieldsValue(),
            rid: id,
            ...data_source_trans(dataSource),
          });
          message.success("创建成功");
        } else {
          await dropcard_config_update({
            ...form.getFieldsValue(),
            rid: id,
            ...data_source_trans(dataSource),
          });
          message.success("修改成功");
        }

        onOk();
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 4 },
    wrapperCol: { span: 20 },
  };

  const on_cancel = () => {
    onCancel();
    setEditingKey("");
    form.resetFields();
  };
  const columns = [
    {
      title: "牌局人数",
      dataIndex: "player",
      valueType: "digit",
      editable: false,
    },
    {
      title: "触发弃牌最小分",
      dataIndex: "min_score",
      valueType: "digit",
      editable: true,
    },
    {
      title: "触发概率",
      dataIndex: "fac",
      valueType: "digit",
      editable: true,
    },
    // {
    //   title: "操作",
    //   valueType: "option",
    //   width: 150,
    //   render: (text, record, _, action) => [
    //     <a
    //       key="editable"
    //       onClick={() => {
    //         //   action?.startEditable?.(record.id);

    //         let state = action?.startEditable?.(record.id);
    //       }}
    //     >
    //       编辑
    //     </a>,
    //     // <a
    //     //   key="delete"
    //     //   onClick={() => {
    //     //     setDataSource(dataSource.filter((item) => item.id !== record.id));
    //     //   }}
    //     // >
    //     //   删除
    //     // </a>,
    //   ],
    // },
    {
      title: "操作",
      dataIndex: "operation",
      width: 100,
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <Space>
            <a onClick={() => save(record.id)}>保存</a>
            <Popconfirm title="Sure to cancel?" onConfirm={cancel}>
              <a>取消</a>
            </Popconfirm>
          </Space>
        ) : (
          <Typography.Link
            disabled={editingKey !== ""}
            onClick={() => edit(record)}
          >
            编辑
          </Typography.Link>
        );
      },
    },
  ];
  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: "number",
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  //获取弃牌设置详情
  const dropcard_config_get_h = async () => {
    let data = await dropcard_config_get({ rid: id });

    if (data) {
      set_state(1);
      form.setFieldsValue({
        ...data,
      });
      let source = [
        {
          id: 2,
          player: 2,
          min_score: data.fighter_2_min_score,
          fac: data.fighter_2_fac,
        },
        {
          id: 3,
          player: 3,
          min_score: data.fighter_3_min_score,
          fac: data.fighter_3_fac,
        },
        {
          id: 4,
          player: 4,
          min_score: data.fighter_4_min_score,
          fac: data.fighter_4_fac,
        },
        {
          id: 5,
          player: 5,
          min_score: data.fighter_5_min_score,
          fac: data.fighter_5_fac,
        },
        {
          id: 6,
          player: 6,
          min_score: data.fighter_6_min_score,
          fac: data.fighter_6_fac,
        },
      ];
      setDataSource(source);
    } else {
      set_state(0);
    }
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        rid: id,
      });
      dropcard_config_get_h();
    }
  }, [visible]);
  return (
    <Modal
      title="弃牌设置"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
      width={820}
      className="DisCardModal"
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="房间ID"
          name="rid"
          rules={[{ required: true, message: "房间id" }]}
        >
          <Input disabled placeholder="请输入房间id" />
        </Form.Item>

        <Form.Item
          label="状态"
          name="status"
          rules={[{ required: true, message: "请选择状态" }]}
        >
          <Select style={{ width: 120 }} placeholder="请选择状态">
            <Option value={0}>开启</Option>
            <Option value={1}>关闭</Option>
          </Select>
        </Form.Item>
        <Form.Item label="弃牌">
          {/* <Card> */}
          {/* <EditableProTable
            rowKey="id"
            headerTitle="可编辑表格"
            maxLength={5}
            columns={columns}
            request={async () => ({
              data: defaultData,
              total: 3,
              success: true,
            })}
            value={dataSource}
            onChange={setDataSource}
            editable={{
              type: "multiple",
              editableKeys,
              onSave: async (rowKey, data, row) => {
                console.log(rowKey, data, row);
                // await waitTime(2000);
              },
              onChange: setEditableRowKeys,
              actionRender: (row, config, defaultDom) => [
                defaultDom.save,
                defaultDom.cancel,
              ],
            }}
          /> */}
          <Form form={Tableform} component={false}>
            <Table
              rowKey="id"
              components={{
                body: {
                  cell: EditableCell,
                },
              }}
              bordered
              dataSource={dataSource}
              columns={mergedColumns}
              rowClassName="editable-row"
              pagination={{
                onChange: cancel,
              }}
            />
          </Form>
          {/* </Card> */}
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
