import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  Radio,
} from "antd";
import { Tree } from "antd";
import {
  discard_config_create,
  discard_config_get,
  discard_config_update,
} from "../../../../api/room";
// import { accrount_create } from "../../../api/acount";
// import { role_create } from "../../../api/role";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, id } = props;
  const [state, set_state] = useState(0); //0为添加，1为修改
  const [form] = Form.useForm();
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        if (state == 0) {
          await discard_config_create({
            ...form.getFieldsValue(),
            rid: id,
          });
          message.success("创建成功");
        } else {
          await discard_config_update({
            ...form.getFieldsValue(),
            rid: id,
          });
          message.success("修改成功");
        }

        onOk();
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  const on_cancel = () => {
    onCancel();
    form.resetFields();
  };
  //获取特殊出牌设置详情
  const discard_config_get_h = async () => {
    let data = await discard_config_get({ rid: id });
    // console.log(data);

    if (data) {
      set_state(1);
      form.setFieldsValue({
        ...data,
      });
    } else {
      set_state(0);
    }
  };
  useEffect(() => {
    if (visible) {
      discard_config_get_h();
      form.setFieldsValue({
        rid: id,
      });
    }
  }, [visible]);
  return (
    <Modal
      title="特殊出牌设置"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="房间ID"
          name="rid"
          rules={[{ required: true, message: "请输入房间ID" }]}
        >
          <Input disabled placeholder="请输入房间ID" />
        </Form.Item>

        <Form.Item
          label="房间池类型"
          name="pool_type"
          rules={[{ required: true, message: "请选择房间池类型" }]}
        >
          <Select placeholder="请选择房间池类型">
            <Option value={1}>1-房间池</Option>
            <Option value={2}>2-个人池</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="平衡状态标志"
          name="risk_flag"
          rules={[{ required: true, message: "请选择平衡状态标志" }]}
        >
          <Select>
            <Option value={1}>1-收分</Option>
            <Option value={2}>2-放分</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="机器人出牌规则"
          name="discard_mode"
          rules={[{ required: true, message: "请输入备注" }]}
        >
          <Radio.Group defaultValue={1}>
            <Radio value={1}>默认出牌</Radio>
            <Radio value={2}>摸张打张</Radio>
          </Radio.Group>
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
