/**
 * 处理sessionStorage 存储超过后，删除时间最后的数据
 * @type {{}}
 */

var cachePrefix = 'pic_';
export const addSessionStorage = (key, storeObj) => {
  //定义一个前缀，表示只删除自己定义的缓存
  // const cachePrefix = 'pic_';
  // sessionStorage对大小是有限制的，所以要进行try catch
  // 500KB左右的东西保存起来就会令到Resources变卡
  // 2M左右就可以令到Resources卡死，操作不了
  // 5M就到了Chrome的极限
  // 超过之后会抛出如下异常：
  // DOMException: Failed to execute 'setItem' on 'Storage': Setting the value of 'basket-http://file.com/ykq/wap/v3Templates/timeout/timeout/large.js' exceeded the quota



  //判断sessionStorage大于500k，删除最后一个session
  var size = 0;
  let item;
  const tempArr = [];
  let obj = sessionStorage;
  for(item in obj) {
    if (item.indexOf(cachePrefix) === 0) {
      tempArr.push(item);
      size += obj.getItem(item).length;
    }
  }

  let sizeK=(size / 1024).toFixed(2);
  if(sizeK > 500 && tempArr.length > 0){
    // 删除缓存时间最早的
    sessionStorage.removeItem(tempArr[0]);
    // 删除后在再添加，利用递归完成
    addSessionStorage(key, storeObj);
  }

  sessionStorage.setItem(cachePrefix + key, storeObj);
};

export const getSessionStorage = (key) => {
  //定义一个前缀，表示只删除自己定义的缓存
  // const cachePrefix = 'pic_';
  // sessionStorage对大小是有限制的，所以要进行try catch
  // 500KB左右的东西保存起来就会令到Resources变卡
  // 2M左右就可以令到Resources卡死，操作不了
  // 5M就到了Chrome的极限
  // 超过之后会抛出如下异常：
  // DOMException: Failed to execute 'setItem' on 'Storage': Setting the value of 'basket-http://file.com/ykq/wap/v3Templates/timeout/timeout/large.js' exceeded the quota
  return sessionStorage.getItem(cachePrefix + key);
};
