import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import { room_config_add, room_get } from "../../../../api/room";
// import { role_create } from "../../../api/role";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk } = props;
  const [roomData, set_roomData] = useState([]);
  const [form] = Form.useForm();
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        await room_config_add({
          ...form.getFieldsValue(),
        });
        message.success("创建成功");
        onOk();
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 9 },
    wrapperCol: { span: 15 },
  };
  //获取房间列表
  const room_get_handle = async () => {
    let data = await room_get({
      page: 1,
      page_size: 100000,
    });
    set_roomData(data.data);
  };
  const on_cancel = () => {
    onCancel();
    form.resetFields();
  };
  useEffect(() => {
    room_get_handle();
  }, []);
  return (
    <Modal
      title="创建规则"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="房间id"
          name="rid"
          rules={[{ required: true, message: "请选择房间id" }]}
        >
          {/* <InputNumber placeholder="请输入房间id" /> */}
          <Select placeholder="请选择房间id">
            {roomData.map((item) => (
              <Option key={item.rid} value={item.rid}>
                {item.rid}
              </Option>
            ))}
          </Select>
        </Form.Item>

        {/* <Form.Item
          label="玩法"
          name="room_type"
          rules={[{ required: true, message: "请输入玩法" }]}
        >
          <Select placeholder="请选择玩法">
            <Option value={1}>1-13Points</Option>
            <Option value={2}>2-10Card</Option>
            <Option value={3}>3-Pool</Option>
          </Select>
        </Form.Item> */}
        <Form.Item
          label="最小真人数量"
          name="human_min"
          rules={[{ required: true, message: "请输入最小真人数量" }]}
        >
          <InputNumber placeholder="请输入最小真人数量" />
        </Form.Item>
        <Form.Item
          label="最大真人数量"
          name="human_max"
          rules={[{ required: true, message: "请输入最大真人数量" }]}
        >
          <InputNumber placeholder="请输入最大真人数量" />
        </Form.Item>
        <Form.Item
          label="最小机器人数量"
          name="wingman_min"
          rules={[{ required: true, message: "请输入最小机器人数量" }]}
        >
          <InputNumber placeholder="请输入最小机器人数量" />
        </Form.Item>
        <Form.Item
          label="最大机器人数量"
          name="wingman_max"
          rules={[{ required: true, message: "请输入最大机器人数量" }]}
        >
          <InputNumber placeholder="请输入最大机器人数量" />
        </Form.Item>

        <Form.Item
          label="最小带入底注倍数"
          name="buy_in_min"
          rules={[{ required: true, message: "请输入最小机器人数量" }]}
        >
          <InputNumber placeholder="请输入最小机器人数量" />
        </Form.Item>
        <Form.Item
          label="最大带入底注倍数"
          name="buy_in_max"
          rules={[{ required: true, message: "请输入最大机器人数量" }]}
        >
          <InputNumber placeholder="请输入最大机器人数量" />
        </Form.Item>

        <Form.Item
          label="最小加入房间时间"
          name="join_interval_min"
          rules={[{ required: true, message: "请输入最小加入房间时间(毫秒)" }]}
        >
          <InputNumber placeholder="请输入最小加入房间时间(毫秒)" />
        </Form.Item>
        <Form.Item
          label="最大加入房间时间"
          name="join_interval_max"
          rules={[{ required: true, message: "请输入最大加入房间时间(毫秒)" }]}
        >
          <InputNumber placeholder="请输入最大加入房间时间(毫秒)" />
        </Form.Item>

        <Form.Item
          label="最小等待入桌超时离桌时间"
          name="wait_2_leave_time_min"
          rules={[
            { required: true, message: "请输入最小等待入桌超时离桌时间(毫秒)" },
          ]}
        >
          <InputNumber placeholder="请输入最小等待入桌超时离桌时间(毫秒)" />
        </Form.Item>
        <Form.Item
          label="最大等待入桌超时离桌时间"
          name="wait_2_leave_time_max"
          rules={[
            { required: true, message: "请输入最大等待入桌超时离桌时间(毫秒)" },
          ]}
        >
          <InputNumber placeholder="请输入最大等待入桌超时离桌时间(毫秒)" />
        </Form.Item>

        <Form.Item
          label="最小离桌局数"
          name="leave_round_min"
          rules={[{ required: true, message: "请输入最小离桌局数" }]}
        >
          <InputNumber placeholder="请输入最小离桌局数" />
        </Form.Item>
        <Form.Item
          label="最大离桌局数"
          name="leave_round_max"
          rules={[{ required: true, message: "请输入最大离桌局数" }]}
        >
          <InputNumber placeholder="请输入最大离桌局数" />
        </Form.Item>

        <Form.Item
          label="最小离桌赢分范围"
          name="leave_win_score_min"
          rules={[{ required: true, message: "请输入最小离桌赢分范围" }]}
        >
          <InputNumber placeholder="请输入最小离桌赢分范围" />
        </Form.Item>
        <Form.Item
          label="最大离桌赢分范围"
          name="leave_win_score_max"
          rules={[{ required: true, message: "请输入最大离桌赢分范围" }]}
        >
          <InputNumber placeholder="请输入最大离桌赢分范围" />
        </Form.Item>

        <Form.Item
          label="最小离桌输分范围"
          name="leave_lose_score_min"
          rules={[{ required: true, message: "请输入离桌输分范围" }]}
        >
          <InputNumber placeholder="请输入离桌输分范围" />
        </Form.Item>
        <Form.Item
          label="最大离桌输分范围"
          name="leave_lose_score_max"
          rules={[{ required: true, message: "请输入离桌输分范围" }]}
        >
          <InputNumber placeholder="请输入离桌输分范围" />
        </Form.Item>

        <Form.Item
          label="最小行为（摸/打/弃）时间"
          name="action_time_min"
          rules={[
            { required: true, message: "请输入最小行为（摸/打/弃）时间(毫秒)" },
          ]}
        >
          <InputNumber placeholder="请输入最小行为（摸/打/弃）时间(毫秒)" />
        </Form.Item>
        <Form.Item
          label="最大行为（摸/打/弃）时间"
          name="action_time_max"
          rules={[
            { required: true, message: "请输入最大行为（摸/打/弃）时间(毫秒)" },
          ]}
        >
          <InputNumber placeholder="请输入最大行为（摸/打/弃）时间(毫秒)" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
