async function getAwsKey(params){
  let resp = await axios.get('/user/awssts',{params})
  let data =resp.data
  let con={
    region: data.region,
    accessKeyId: data.access_key_id,
    secretAccessKey: data.secret_access_key,
    session_token:data.session_token,
    bucket:data.bucket,
    appName:data.app_name,
    acl:data.acl,
  }
  return con
}

