const s3Client = new AWS.S3();

/**
 * AWS S3下载文件
 */
export async function downloadFromS3(fileName,appid,dir="") {
  var acces = getSessionStorage("accessKeyId");
  let con = JSON.parse(acces);
  if(!con){
    con = await getAwsKey(appid);
    addSessionStorage("accessKeyId",JSON.stringify(con));
  }
  //poker文件扔到appname通用文件中
  if(fileName.indexOf("poker") !== -1 && con.appName){
    fileName=con.appName+fileName
  }
  AWS.config.update(
      {
        region: con.region,
        accessKeyId: con.accessKeyId,
        secretAccessKey: con.secretAccessKey,
      }
  );
  AWS.config.credentials.sessionToken=con.session_token;

  return new Promise((resolve, _reject) => {
    const params = { Bucket: con.bucket, Key: dir+ fileName};
    new AWS.S3().getObject(params, (error, data) => {
      if (error) {
        _reject(error);
      }
      if (data) {
        resolve(data.Body);
      }
    });
  });
}



export async function download(fileName) {
  let result = null;
  try {
    result = await downloadFromS3(fileName);
  } catch (error) {
    result = error;
  } finally {
    console.dir(result);
  }
}

export const awsSave = async  (tempPath, fileName) => {
  return new Promise((resolve, reject) => {
    // 文件读取
    fs.readFile(tempPath, (err, data) => {
      if (err) {
        reject(err);
      } else {
        const params = {
          Bucket: bucket,
          Key: fileName,
          Body: data,
          // ACL: aws.acl,
        };
        s3Client.upload(params, null, (uploadErr, data) => {
          if (uploadErr) {
            reject(uploadErr);
          } else {
            resolve(data.Location);
          }
        });
      }
    });
  })
};

export const awsUploadFile = async  (data, fileName,appid,dir="") => {
  let con = await getAwsKey({appid});
  AWS.config.update(
      {
        region: con.region,
        accessKeyId: con.accessKeyId,
        secretAccessKey: con.secretAccessKey,
      }
  );
  // //判断图片文件格式
  // bytesToHexString(data);
  let lastIndexTmp=fileName.lastIndexOf(".")
  let suffer= fileName.substr(lastIndexTmp);
  let name = fileName.substr(0,lastIndexTmp);
  //文件名称唯一
  let keyName = con.appName+"/images/"+dir+dayjs.utc().unix()+md5(name)+suffer
  AWS.config.credentials.sessionToken=con.session_token;
  return new Promise((resolve, reject) => {
        const params = {
          Bucket: con.bucket,
          Key: keyName,
          Body: data,
          ACL: con.acl,
        };
          new AWS.S3().upload(params, null, (uploadErr, data) => {
          if (uploadErr) {
            reject(uploadErr);
          } else {
            // resolve(data.Location);
            resolve(keyName); //存储相对路径
          }
        });
  })
};

function bytesToHexString(data){
  let res=""
  if (data == null || data.length <= 0) {
    return null;
  }

  for (let i = 0 ;i<data.length;i++){
    let v = data[i] & 0xFF;
    let hv = v.toString();
    if (hv.length < 2){
      res += 0
    }
    res += hv
  }
  return res
}

/**
 * AWS S3上传文件
 */
export async function awsUpload(tempPath, fileName,appid,dir="") {
  //如果为服务器上传，直接返回
  if (!s3Enable) {
    return "false"
  }

  let result = null;
  try {
    result = await awsUploadFile(tempPath, fileName,appid,dir);
  } catch (error) {
    console.error(error);
    message.error("upload failed");
  } finally {
    // console.dir(result);
  }
  return result

}

